*{
  background-color: #b1bcd3;
}

.center-div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px;
}


.App-header {
  width: 412px;
  height: 41px;
  color: #293264;
  text-align: center;
  font-family: Karla;
  font-size: 45px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.btn{
  width: 193px;
  height: 52px;
  border-radius: 15px;
  background: #25347e;
  color: #d0dde3;
  cursor: pointer;
  margin-top: 30px;
  font-family: karla;
  font-size: 30px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.next-div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 40px;
}

.btn-ans{
  margin: 10px 8px;
  margin-left: 0px;
  margin-right: 20px;
  border-radius: 9px;
  min-width: 90px;
  cursor: pointer;
}

.question{
  padding:30px;
  outline-width: 10px;
  outline-style: groove;
  outline-color: #4D5B9E;

}

.selected {
  background-color:#0e2e14;
}
